.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    max-width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-Content {
    background-color: #282c34;
    min-height: 100vh;
    max-width: 100vw;
    display: flex;
    flex-direction: column;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

.divider {
    width: 100%;
    height: 1px;
    background: #ffffff;
}

.divider-dash {
    width: 100%;
    height: 1px;
    margin-top: 20px;
    margin-bottom: 20px;
    background: repeating-linear-gradient(90deg, #ffffff, #ffffff 5px, transparent 5px, transparent 10px);
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
