body {
    /*background-color: #f5f7fa;*/
    color: #333;
    line-height: 1.6;
    /*padding: 20px;*/
}

.container {
    max-width: 800px;
    margin: 0 auto;
    background: white;
    border-radius: 10px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    padding: 30px;
}

h1 {
    text-align: center;
    margin-bottom: 30px;
    color: #2c3e50;
    font-weight: 600;
}

.form-group {
    margin-bottom: 20px;
}

.form-group-center {
    margin-right: 40px;
    margin-bottom: 20px;
}

.form-row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px;
}

.form-col {
    flex: 1;
    padding: 0 10px;
    min-width: 200px;
}

label {
    display: block;
    margin-bottom: 8px;
    font-weight: 500;
    color: #555;
}

input {
    width: 100%;
    padding: 12px 15px;
    border: 1px solid #ddd;
    border-radius: 6px;
    font-size: 16px;
    transition: border 0.3s, box-shadow 0.3s;
}

input:focus {
    outline: none;
    border-color: #4a90e2;
    box-shadow: 0 0 0 3px rgba(74, 144, 226, 0.2);
}

.params-section {
    margin-top: 30px;
}

.params-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
}

.add-param-btn {
    background-color: #4CAF50;
    color: white;
    border: none;
    padding: 8px 15px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
    transition: background-color 0.3s;
}

.add-param-btn:hover {
    background-color: #45a049;
}

.param-item {
    display: flex;
    margin-bottom: 15px;
    align-items: center;
}

.param-input {
    flex: 1;
    margin-right: 10px;
}

.remove-param-btn {
    background-color: #f44336;
    color: white;
    border: none;
    padding: 8px 12px;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.copy {
    background-color: #fff;
    color: #000;
    padding: 2px 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 13px;
    transition: background-color 0.3s;
    margin-bottom: 8px;
}

.submit-btn {
    display: block;
    width: 100%;
    background-color: #2196F3;
    color: white;
    border: none;
    padding: 14px;
    border-radius: 6px;
    font-size: 16px;
    cursor: pointer;
    margin-top: 30px;
    transition: background-color 0.3s;
}

.submit-btn:hover {
    background-color: #0b7dda;
}

.action-card {
    overflow: hidden;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
    transition: transform 0.3s ease;
    margin-bottom: 20px;
}

.result-section {
    margin-top: 30px;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 6px;
    border-left: 4px solid #2196F3;
}

.result-title {
    font-weight: 600;
    margin-bottom: 10px;
    color: #2c3e50;
}

.preview-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 25px;
}

.preview-card {
    background: #f8f9fa;
    border-radius: 12px;
    overflow: hidden;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
    transition: transform 0.3s ease;
}

.preview-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
    display: block;
}

.preview-info {
    padding: 15px;
}

.preview-name {
    font-weight: bold;
    margin-bottom: 8px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.preview-details {
    font-size: 0.85rem;
    color: #7f8c8d;
}

.preview-details div {
    margin-bottom: 5px;
}

pre {
    background-color: #2d2d2d;
    color: #f8f8f2;
    padding: 15px;
    border-radius: 6px;
    overflow-x: auto;
    font-size: 14px;
    line-height: 1.4;
}

